class SuyaoMap {
  constructor(){
    this.stars  = Array.from("è§’äº¢æ°æˆ¿å¿ƒå°¾ç®•æ–—å¥³è™šå±å®¤å£å¥å¨„èƒƒæ˜‚æ¯•è§œå‚äº•é¬¼æŸ³æ˜Ÿå¼ ç¿¼è½¸");
    this.keys = Array.from("ç¿¼è§’æ°å¿ƒç®•å¥³å®¤å¨„æ˜‚è§œé¬¼æ˜Ÿ"); // æœˆå
    this.map = this.keys.map((key, i) => {
      return this.renderMap(key);
    });
  }
  /**
   * æ ¹æ®æœˆåè¿”å›å½“å®¿æ›œæœˆå†
   * @param {String} key 
   */
   renderMap(star) {
    let starIndex = this.stars.findIndex(e => e === star);
    let len = 14;
    let m = new Array(30);

    m[14] = star;
    for(let i = 0; i < len; i++){
      m[13-i] = this.stars[(starIndex-i-1+27)%27];
    }
    len = 15;
    for(let i = 0; i < len; i++) {
      //æŠŠåäº”ä»¥åçš„å¡«å……å¥½
      m[15+i] = this.stars[(starIndex+i+1)%27];
    }
    return m;
  }

  /***
   * é€šè¿‡å†œå†å¹´æœˆï¼Œè·å–å‘½å®¿
   * @param {Integer} month 
   * @param {Integer} day 
   */
  getStar = (month, day) => {
    if(isNaN(month) || isNaN(day) || month < 0 || day < 0){
      console.error(`SuyaoMap.getStar error: month is ${month}, day is ${day}`);
      return null;
    }
    return this.map[month-1][day-1]
  }
  
  /**
   * è·å–æŸä¸ªæ˜Ÿå®¿çš„æ˜Ÿç›˜
   * @param {CharacterData} star
   */
  getChart = (star) => {
    let chart = new Array(27);
    let index = this.stars.indexOf(star);
    for(let i = 0 ; i < this.stars.length; i++){
      chart[(i+27+13-index)%27] = this.stars[i];
    }

    let secrets = ['æˆ','å','å‹','äº²','èƒ','è£','è¡°','å®‰','å±','æˆ','å','å‹','äº²','å‘½', 'è£','è¡°','å®‰','å±','æˆ','å','å‹','äº²','ä¸š','è£','è¡°','å®‰','å±'];
    chart = chart.map((star,index) => {
      return {
        star,
        secret: secrets[index]
      }
    });
    let distances = ['è¿œ','è¿œ','è¿œ','è¿œ','è¿œ','ä¸­','ä¸­','ä¸­','ä¸­','è¿‘','è¿‘','è¿‘','è¿‘','å‘½','è¿‘','è¿‘','è¿‘','è¿‘','ä¸­','ä¸­','ä¸­','ä¸­','è¿œ','è¿œ','è¿œ','è¿œ','è¿œ']
    chart = chart.map((star, index) => {
      return {
        ...star,
        distance: distances[index]
      }
    })
    return chart;
  }

  getStarArray = () => {
    return this.stars;
  }

}



export default SuyaoMap;